import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { DeliveriesTimes } from '@core/interfaces/deliveries-times-interface';
import {
  ApiDataResponse,
  ApiListResponse,
} from '@core/interfaces/http.interfaces';
import { environment } from '@environments/environment';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class DeliveryTimesService {
  public endpoint: string = environment.endpoint;
  constructor(private http: HttpClient) {}

  updateDeliveryTimes(
    delivery: DeliveriesTimes
  ): Observable<ApiDataResponse<DeliveriesTimes>> {
    return this.http.put<ApiDataResponse<DeliveriesTimes>>(
      environment.endpoint + `/deliveries-times/update`,
      delivery
    );
  }

  getDeliveryTimesList(): Observable<ApiListResponse<DeliveriesTimes>> {
    return this.http.get<ApiListResponse<DeliveriesTimes>>(
      environment.endpoint + `/deliveries-times/list`
    );
  }
}
